






















































import Vue from 'vue';
import { FExpand, FForm } from '@fkui/vue';
import { focus } from '@fkui/logic';

export default Vue.extend({
    name: 'Container',
    components: {
        FExpand,
        FForm,
    },
    model: {
        event: 'steg-klart-event',
    },
    props: {
        rubrik: {
            type: String,
            default: 'Rubrik',
        },
        vilketSteg: {
            type: String,
            default: '0',
        },
        antalSteg: {
            type: String,
            default: '0',
        },
        vilketStateSteg: {
            type: String,
            default: '0',
        },
    },
    data: () => ({
        oppetSteg: false,
    }),
    computed: {},
    methods: {
        oppnaSteg(): void {
            this.oppetSteg = !this.oppetSteg;
            this.$nextTick(() => {
                const el = document.querySelector<HTMLElement>('.aktivtContainersteg .rubrik');
                if (el) {
                    focus(el);
                    window.scrollTo({ top: 180, behavior: 'smooth' });
                }
            });
        },
        stegKlart(): void {
            this.oppetSteg = false;
            this.$emit('steg-klart-event');
        },
    },
});
