














































































import Vue from 'vue';
import Container from '@/components/Container.vue';
import Steg1Content from '@/components/data/Steg1content.vue';
import Steg2Content from '@/components/data/Steg2content.vue';
import Steg3Content from '@/components/data/Steg3content.vue';
import Steg4Content from '@/components/data/Steg4content.vue';
import Steg5Content from '@/components/data/Steg5content.vue';

export default Vue.extend({
    name: 'Home',
    components: {
        Container,
        Steg1Content,
        Steg2Content,
        Steg3Content,
        Steg4Content,
        Steg5Content,
    },
    data: () => ({
        steg1State: '1',
        steg2State: '0',
        steg3State: '0',
        steg4State: '0',
        steg5State: '0',
        finishCounter: 0,
    }),
    methods: {
        updateraStegStateForsta(vilketSteg: string): void {
            if (vilketSteg === '1') {
                this.steg1State = '2';
                this.finishCounter++;
                this.steg2State = '1';
                (this.$refs.oppnaContainer2 as any).oppnaSteg();
            } else if (vilketSteg === '2') {
                this.steg2State = '2';
                this.finishCounter++;
                this.steg3State = '1';
                (this.$refs.oppnaContainer3 as any).oppnaSteg();
            } else if (vilketSteg === '3') {
                this.steg3State = '2';
                this.finishCounter++;
                this.steg4State = '1';
                (this.$refs.oppnaContainer4 as any).oppnaSteg();
            }
        },
        updateraStegStateAndra(vilketSteg: string): void {
            if (vilketSteg === '4') {
                this.steg4State = '2';
                this.finishCounter++;
                this.steg5State = '1';
                (this.$refs.oppnaContainer5 as any).oppnaSteg();
            } else if (vilketSteg === '5') {
                this.finishCounter++;
                this.steg5State = '2';
            }
        },
        gaTillGranskning(): void {
            if (this.finishCounter === 5) {
                console.log('visa ansökan');
            }
        },
    },
});
